<template>
<div>
    <div v-if="isLoading" class="center pt-5">
        <div class="spinner-border text-primary" role="status">
            <span class="sr-only">Loading...</span>
        </div>
    </div>
    <DeptoBase
    :goal="vigueta.meta_semana"
    :progress="vigueta.progress"
    :name="vigueta.nombre"
    :depto="vigueta.depto"
    :days="vigueta.dias_laborados"
    :m3="vigueta.m3_persona"
    :bonus_p="vigueta.bono_persona"
    :bonus_tot="vigueta.bono_depto"
    :asis="vigueta.asistencia"
    :extra="vigueta.$_extra_m3"
    />
</div>
</template>


<script>

import DeptoBase from '@/components/DeptoBase';

export default {
    name: 'Vigueta',
    components: {DeptoBase},
    created() {
        this.$store.dispatch("vigueta/getInfo");
    },
    computed: {
        isLoading() {
            return this.$store.getters["vigueta/loading"];
        },
        vigueta() {
        return this.$store.getters["vigueta/data"];
        }
    }
}
</script>